.pinabetes {
    color: var(--gray);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pinabetes__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.pinabetes > *  {
    margin: 1rem 0 0 0;
}

.pinabetes__info {
    text-align: center;
    width: 90%;
    display: none;
}

.calidad { 
    font-weight: 400;
    font-size: 1.1rem;
}

.radio{
    align-items: center;
    border-radius: 1rem;
    color: var(--gray);
    cursor: pointer;
    display: flex;
    justify-content: space-around;
    margin-top: 0.6rem;
    overflow: hidden;
    box-shadow:0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06)
}

.radio__input {
    display: none;
}
.radio__label {
    padding: 0.7rem 1rem;
    cursor: pointer;
    text-align: center;
}

.radio__label--active {
    background-color: var(--red);
    color: var(--white);
    padding-left: 1.3rem;
}

.card {
    align-items: center;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin-bottom: 1.2rem;
}

.card__img {
    border-radius: 4px; 
    border: 1px solid #ddd;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    padding: 5px;
    max-width: 40%;
    cursor: pointer;
}

.card__content > * {
    margin: 0;
    padding: 0;
}

.card__content { 
    flex-grow: 1;
    text-align: center;
}

.card__container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    margin: 1rem auto;
}

.precios {
    width: 80%;
    text-align: center;
}

@media (min-width: 992px) { 

    .pinabetes {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 2rem repeat(auto-fill, 1fr);
        place-items: center;
        gap: 1.2rem;
        margin-top: 2rem
    }

    .pinabetes__container { 
        overflow: scroll;
        height: 80vh;
        /* scroll */
    }

    .radio {
        overflow: clip;
    }

    .pinabetes__container2 { 
        grid-column:  1 / 2;
        grid-row:  1 / 2;
        place-self: start center;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        height: 80vh;
    }
    
    .pinabetes__info {
        display: block;
        width: 65%;
    }

}